import React, { PureComponent } from 'react';
import { Switch, Route, Redirect } from 'react-router-dom';
import Channel from "./loadable";
import EditChannel from './edit/loadable';
import Advertise from './advertise/loadable';

class ChannelRouter extends PureComponent {
  render() {
    return (
      <Switch>
        <Route path='/build/channel/index' component={Channel} />
        <Route path='/build/channel/create' component={EditChannel} />
        <Route path='/build/channel/update' component={EditChannel} />
        <Route path='/build/channel/advertise' component={Advertise} />
        <Redirect exact from="/build/channel" to="/build/channel/index" />
      </Switch>
    )
  }
}

export default ChannelRouter;