import React, {PureComponent} from 'react';
import {Link, withRouter} from 'react-router-dom';
import {Menu} from 'antd';

const SubMenu = Menu.SubMenu;
const MenuItem = Menu.Item;

class Slider extends PureComponent {
  render() {
    const SliderWrapper = {
      width: '240px',
      height: '100vh',
      position: 'fixed',
      top: '65px',
      left: 0,
      borderRight: '1px solid #eee'
    };
    return (
      <div style={SliderWrapper}>
        <Menu
          mode="inline"
          defaultSelectedKeys={['novel']}
        >
          {/*<MenuItem key="home">*/}
            {/*<Link to='/build'>首页</Link>*/}
          {/*</MenuItem>*/}
          <MenuItem key="novel">
            <Link to='/build/novel'>小说管理</Link>
          </MenuItem>
          <MenuItem key="channel">
            <Link to='/build/channel'>渠道管理</Link>
          </MenuItem>
          {/*<SubMenu title={<span>媒体管理</span>}>*/}
            {/*<MenuItem>*/}
              {/*<Link to='/build/media/create'>创建媒体</Link>*/}
            {/*</MenuItem>*/}
            {/*<MenuItem>*/}
              {/*<Link to='/build/media/show'>媒体列表</Link>*/}
            {/*</MenuItem>*/}
          {/*</SubMenu>*/}
          {/*<SubMenu title={<span>广告位管理</span>}>*/}
            {/*<MenuItem>*/}
              {/*<Link to='/build/advertise/create'>创建广告位</Link>*/}
            {/*</MenuItem>*/}
            {/*<MenuItem>*/}
              {/*<Link to='/build/advertise/show'>广告位列表</Link>*/}
            {/*</MenuItem>*/}
          {/*</SubMenu>*/}
        </Menu>
      </div>
    )
  }
  handleClick() {

  }
}

export default withRouter(Slider);