import React, { PureComponent } from 'react';
import { Switch, Route, Redirect } from 'react-router-dom';

import Novel from './loadable';
import Modify from "./modify/loadable";
import Config from "./config/loadable";
import CreateRule from "./config/create/loadable";

class NovelRouter extends PureComponent {
  render() {
    return (
      <Switch>
        <Route path='/build/novel/index' component={Novel} />
        <Route path='/build/novel/modify' component={Modify} />
        <Route path='/build/novel/config/index' component={Config} />
        <Route path='/build/novel/config/create' component={CreateRule} />
        <Redirect exact from="/build/novel" to="/build/novel/index" />
        <Redirect exact from="/build/novel/config" to="/build/novel/config/index" />
      </Switch>
    )
  }
}

export default NovelRouter;